@charset 'UTF-8';

/* @FONT-FACE loads font into browser */
@font-face {
    font-family: 'typicons';
    font-weight: normal;
    font-style: normal;
    src: url("@{assetUrl}fonts/typicons/typicons.eot");
    src: url("@{assetUrl}fonts/typicons/typicons.eot?#iefix") format('embedded-opentype'),
    url("@{assetUrl}fonts/typicons/typicons.woff") format('woff'),
    url("@{assetUrl}fonts/typicons/typicons.ttf") format('truetype'),
    url("@{assetUrl}fonts/typicons/typicons.svg#typicons") format('svg');
}

.typ {
    position: relative;
}

/* :before psuedo-selector inserts and styles icon */
.typ:before {
    font-family: 'typicons';
    font-style: normal;
    font-weight: normal;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    height: 1em;
    font-size: 1em;
    text-align: center;
    -webkit-font-smoothing: antialiased;
    font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
}

